body {
  background-color: rgb(66, 141, 141);
  color:#fff;
 }
 h1 {
  text-align: center;
  color: black;
 }
 fieldset {
  margin-left: 20%;
  margin-right: 20%;
  background-color: rgb(23, 49, 41);
 }
 input {
  padding: .3rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 10px;
 }
 label {
  margin: 10px;
 }
 button {
  padding: .3rem;
  font-size: 15px;
  background-color: rgb(9, 17, 6);
  border: 4px solid #ccc;
  color: #ccc;
  padding: 10px;
  border-radius: 8px;
  margin: 10px;
  cursor: pointer;
 }